import Table from 'react-bootstrap/Table';
import carousel from '../assets/carousels1.webp'
import React, { useState } from 'react'
import {Link} from 'react-router-dom'
import { useDispatch,useSelector } from 'react-redux';
import { remove } from '../redux/Slices/cartSlice';

const Cart=()=> {
  const dispatch=useDispatch()
  const products=useSelector((state)=>state.cart)
  const removeToCart=(id)=>{
      dispatch(remove(id))
  }
  return (
    <div className="container-fluid mt-5 cart-page">
        <Table>
      <thead>
        <tr>
          
          <th>Item</th>
          <th>Title</th>
          <th>Price</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        {products.map((item,index)=>{
          return (
            <tr className='cart-item'>
            <td><img src={item.image} alt="" /></td>
            <td>{item.title}</td>
            <td>{item.price} $</td>
            <td className='cart-btn'>
              <button className='btn btn-outline-danger m-1 remove' onClick={()=>removeToCart(item.id)} >Remove</button>
              <Link to='/checkout'><button className='btn btn-outline-warning m-1 checkout'>Checkout</button></Link>
              </td>
          </tr>

          )
        })}
      </tbody>
    </Table>
    </div>
    
  );
}

export default Cart;