import React, { useEffect, useState } from "react";
import carousel from "../assets/carousels1.webp";
import { useDispatch, useSelector } from "react-redux";
import { add } from "../redux/Slices/cartSlice";
import { Link } from "react-router-dom";
import { addDetails } from "../redux/Slices/singleProduct";
const Products = () => {
  const [products, setProducts] = useState([]);
  const cartProducts = useSelector((state) => state.cart);
  const singleproduct=useSelector(state=>state.single)

  const dispatch = useDispatch();

  useEffect(() => {
    fetch("https://fakestoreapi.com/products")
      .then((data) => data.json())
      .then((result) => setProducts(result));
  }, []);

  const addToCart = (item) => {
    dispatch(add(item));
    console.log(cartProducts.length + "products are added into cart");
  };
const Details=(item)=>{
  dispatch(addDetails(item))
}
  return (
    <>
      <div className="container products-page mt-5">
        <div className="row mt-5">
          {products.map((item, index) => {
            return (
              <div className="col-lg-3 col-md-4 col-sm-6 product" key={index}>
                <div className="image p-2">
                  <img src={item.image} alt="" />
                </div>
                <div className="title-price">
                  <p className="title">{item.title}</p>
                  <h4 className="price">Price: {item.price}</h4>
                </div>
                <div className="buttons">
                  <button
                    className="btn btn-outline-warning prod-page-btn"
                    onClick={() => addToCart(item)}
                  >
                    Add to Cart
                  </button>
                  <Link
                    to={`/singleproduct/${item.id}`}
                    style={{ color: "inherit", textDecoration: "inherit" }}
                  >
                   {/*  <button className="btn btn-outline-dark prod-page-btn " onClick={()=>Details(item.id)}>
                      More Info
                    </button> */}
                  </Link>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default Products;
