import { createSlice } from "@reduxjs/toolkit";

const singleProduct=createSlice({
    name:'single',
    initialState:[],
    reducers:{
        addDetails:(state,action)=>{
            state.filter(item=>item.id==action.payload).push(action.payload)
        }
    }
})

export default singleProduct.reducer
export const{addDetails}=singleProduct.actions