import React from 'react'
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

const Checkout = () => {
  return (
    <>
    <div className="checkout-page">
    <div className="container">
      <div className="row">
        <div className="col">
          <h5 className='bg-warning p-3 mt-5 mb-5'>Total: </h5>
        </div>
      </div>
      <div className="row">
        <div className="col">
          <div className="checkout-form">
          <Form.Floating className="mb-3">
        <Form.Control
          id="floatingInputCustom"
          type="email"
          placeholder="Email Address"
        />
        <label htmlFor="floatingInputCustom">Email address</label>
      </Form.Floating>
      <Form.Floating className='mb-3'>
        <Form.Control
          id="floatingPasswordCustom"
          type="text"
          placeholder="First Name"
        />
        <label htmlFor="floatingPasswordCustom">First Name</label>
      </Form.Floating >
      <Form.Floating className="mb-3">
        <Form.Control
          id="floatingPasswordCustom"
          type="text"
          placeholder="Last Name"
        />
        <label htmlFor="floatingPasswordCustom">Last Name</label>
      </Form.Floating>
      <Form.Floating className='mb-3'>
        <Form.Control
          id="floatingPasswordCustom"
          type="text"
          placeholder="Address"
        />
        <label htmlFor="floatingPasswordCustom">Address</label>
      </Form.Floating>
      <Form.Floating className='mb-3'>
        <Form.Control
          id="floatingPasswordCustom"
          type="text"
          placeholder="Apartment, etc."
        />
        <label htmlFor="floatingPasswordCustom">Apartment , etc.</label>
      </Form.Floating>
      <Form.Floating className='mb-3'>
        <Form.Control
          id="floatingPasswordCustom"
          type="text"
          placeholder="City"
        />
        <label htmlFor="floatingPasswordCustom">City</label>
      </Form.Floating>
      <Form.Floating className='mb-3'>
        <Form.Control
          id="floatingPasswordCustom"
          type="text"
          placeholder="Postal code"
        />
        <label htmlFor="floatingPasswordCustom">Postal Code</label>
      </Form.Floating>
      <Form.Floating className='mb-3'>
        <Form.Control
          id="floatingPasswordCustom"
          type="text"
          placeholder="Phone"
        />
        <label htmlFor="floatingPasswordCustom">Phone</label>
      </Form.Floating>
      <Button variant="dark" className='mb-5 w-100 p-3'>Place Order</Button>
          </div>
        </div>
      </div>
    </div>
    
    </div>
    
    </>
  )
}

export default Checkout