import React, { useState } from "react";
import { Link } from "react-router-dom";
import { TfiShoppingCartFull } from "react-icons/tfi";
import { useSelector } from "react-redux";
import {
  CNavbar,
  CNavbarBrand,
  CContainer,
  CNavbarToggler,
  COffcanvas,
  COffcanvasBody,
  COffcanvasHeader,
  COffcanvasTitle,
  CCloseButton,
  CNavbarNav,
  CNavItem,
  CNavLink,
  CDropdown,
  CDropdownToggle,
  CDropdownMenu,
  CDropdownItem,
  CDropdownDivider,
  CForm,
  CFormInput,
  CButton,
} from "@coreui/react";

const Navbar = () => {
  const [visible, setVisible] = useState(false);
  const products = useSelector((state) => state.cart);
  return (
    <CNavbar
      colorScheme="light"
      className="bg-light fixed-top mb-5"
      expand="xxl"
    >
      <CContainer fluid>
        <CNavbarToggler
          aria-controls="offcanvasNavbar2"
          aria-label="Toggle navigation"
          onClick={() => setVisible(!visible)}
          className="me-auto"
        />
        <CNavbarBrand className="">
          <Link
            to="/cart"
            style={{ color: "inherit", textDecoration: "inherit" }}
          >
            <TfiShoppingCartFull />
            {products.length}
          </Link>
        </CNavbarBrand>

        <COffcanvas
          id="offcanvasNavbar2"
          placement="start"
          portal={false}
          visible={visible}
          onHide={() => setVisible(false)}
        >
          <COffcanvasHeader>
            <COffcanvasTitle>Style Market</COffcanvasTitle>
            <CCloseButton
              className="text-reset"
              onClick={() => setVisible(false)}
            />
          </COffcanvasHeader>
          <COffcanvasBody>
            <CNavbarNav>
              <CNavItem>
                <Link
                  to="/"
                  style={{ color: "inherit", textDecoration: "inherit" }}
                >
                  <CNavLink active>Home</CNavLink>
                </Link>
              </CNavItem>
              <CNavItem>
                <Link
                  to="/cart"
                  style={{ color: "inherit", textDecoration: "inherit" }}
                >
                  <CNavLink active>Cart</CNavLink>
                </Link>
              </CNavItem>
            </CNavbarNav>
            <CForm className="d-flex">
              <CFormInput type="search" className="me-2" placeholder="Search" />
              <CButton type="submit" color="success" variant="outline">
                Search
              </CButton>
            </CForm>
          </COffcanvasBody>
        </COffcanvas>
      </CContainer>
    </CNavbar>
  );
};

export default Navbar;
